export default function FloatingOrbs({ orbsRef, count = 5 }) {
  return (
    <div className="absolute inset-0 -z-10">
      {[...Array(count)].map((_, i) => (
        <div
          key={i}
          ref={(el) => (orbsRef.current[i] = el)}
          className=" w-[500px] rounded-full blur-3xl opacity-40"
          style={{
            top: `${10 + i * 15}%`,
            left: `${(i * 19) % 90}%`,
            background:
              i % 2 === 0
                ? 'radial-gradient(circle at 30% 30%, rgba(34,211,238,0.35), transparent 60%)'
                : 'radial-gradient(circle at 70% 70%, rgba(99,102,241,0.35), transparent 60%)',
          }}
        />
      ))}
    </div>
  )
}
