export default function BackgroundGrid() {
  return (
    <div aria-hidden className="pointer-events-none absolute inset-0 overflow-hidden">
      <div className="neon-glow absolute inset-0" />
      <div className="neon-grid absolute inset-0" />
      <div className="neon-grid neon-grid--blur absolute inset-0" />
      <style jsx>{`
        .neon-glow {
          background:
            radial-gradient(1000px 600px at 80% -20%, rgba(34, 211, 238, 0.18), transparent 70%),
            radial-gradient(800px 500px at 20% 120%, rgba(99, 102, 241, 0.18), transparent 70%);
        }

        .neon-grid {
          background-image:
            linear-gradient(to right, rgba(0, 255, 255, 0.25) 1px, transparent 1px),
            linear-gradient(to bottom, rgba(255, 0, 255, 0.25) 1px, transparent 1px);
          background-size: 60px 60px;
          background-position: 0 0, 0 0;
          mix-blend-mode: screen;
          opacity: 0.25;
          animation: move-grid 12s linear infinite;
        }

        .neon-grid--blur {
          filter: blur(6px) brightness(1.2) saturate(1.3);
          opacity: 0.35;
          animation: move-grid 12s linear infinite, hue 20s linear infinite;
        }

        @keyframes move-grid {
          0% { background-position: 0 0, 0 0; }
          100% { background-position: 60px 0, 0 60px; }
        }

        @keyframes hue {
          0% { filter: blur(6px) brightness(1.2) saturate(1.3) hue-rotate(0deg); }
          100% { filter: blur(6px) brightness(1.2) saturate(1.3) hue-rotate(360deg); }
        }

        @media (prefers-reduced-motion: reduce) {
          .neon-grid,
          .neon-grid--blur {
            animation: none;
          }
        }
      `}</style>
    </div>
  )
}
